import axios from 'axios';

class Auth {
    get token() {
        return localStorage.getItem('token') || '';
    }
    get headers() {
        return {
            headers: {
                'x-token': this.token
            }
        };
    }

    async login(usuario, password, props) {
        console.log(`${process.env.REACT_APP_URL}/login`);
        try {
            await axios
                .post(`${process.env.REACT_APP_URL}/login`, {
                    usuario,
                    password
                })
                .then((resp) => {
                    localStorage.setItem('token', resp.data.token);
                    //this.authenticated = true;
                    props.history.push('/fform');
                })
                .catch((err) => {
                    console.log(err);
                    alert('error al loguearse', '', 'error');
                });
        } catch (e) {
            console.log(e);
        }
    }

    logout(props) {
        this.authenticated = false;
        props.history.push('/');
        localStorage.removeItem('token');
    }

    validarToken() {
        return axios
            .get(`${process.env.REACT_APP_URL}/login/renew`, this.headers)
            .then((resp) => console.log(resp));
    }

    /* isAuthenticated() {
    console.log(this.authenticated);
    return this.authenticated
  } */
}

export default new Auth();
