import {
    Button,
    Checkbox,
    Paper,
    TextField,
    Typography
} from '@material-ui/core';
import bannerJor from '../assets/portyt.png';
import hsf from '../assets/hacemosLogo.png';
import { useState } from 'react';
import { isMobile } from 'react-device-detect';
import { GoogleReCaptchaProvider } from 'react-google-recaptcha-v3';
import sumateService from '../helpers/sumateService';
import useProgressiveImage from '../helpers/progressiveImg';
import Loader from '../components/loader';

const FormuPart = () => {
    const imag = useProgressiveImage(bannerJor);
    const [formu, setFormu] = useState({
        nombre: '',
        apellido: '',
        dni: '',
        tel: '',
        email: '',
        ayuda: []
    });
    if (!imag) {
        return <Loader />;
    }

    function validateForm() {
        return (
            formu.nombre.length > 0 &&
            formu.apellido.length > 0 &&
            formu.dni.length > 0 &&
            formu.tel.length > 0 &&
            formu.ayuda.length > 0
        );
    }

    async function handlerSubmit() {
        await sumateService.crearSumate(formu);
    }

    return (
        <GoogleReCaptchaProvider
            reCaptchaKey="6LdYrcQbAAAAAM78NjeWXTImZjXMRP-dLU-B8CEv"
            language="es"
            useEnterprise
            scriptProps={{
                async: false, // optional, default to false,
                defer: false, // optional, default to false
                appendTo: 'body', // optional, default to "head", can be "head" or "body",
                nonce: undefined // optional, default undefined
            }}
        >
            <div>
                <img
                    alt="hacemos santa fe"
                    style={{ height: isMobile ? 60 : 80, margin: 10 }}
                    src={hsf}
                />
            </div>
            <div
                style={{
                    display: 'flex',
                    backgroundImage: `url(${imag})`,
                    backgroundSize: 'cover',
                    width: '100%'
                }}
            >
                <Paper
                    style={{
                        flexDirection: 'column',
                        display: 'flex',
                        width: isMobile ? '80%' : 350,
                        marginLeft: isMobile ? 30 : 40,
                        marginTop: 20,
                        marginBottom: 10
                    }}
                >
                    <div
                        style={{
                            backgroundColor: 'rgba(61, 194, 227)',
                            height: 50
                        }}
                    >
                        <Typography
                            style={{
                                fontSize: 22,
                                padding: 10,
                                fontWeight: 'bold',
                                color: '#fff'
                            }}
                        >
                            SUMATE
                        </Typography>
                    </div>
                    <div
                        style={{
                            padding: 20,
                            display: 'flex',
                            flexDirection: 'column'
                        }}
                    >
                        {textFi.map((txtF) => (
                            <div style={{ marginBottom: 20 }}>
                                <Typography>{txtF.name}</Typography>
                                <TextField
                                    size="small"
                                    variant="outlined"
                                    type={txtF.type}
                                    onChange={(e) =>
                                        setFormu({
                                            ...formu,
                                            [txtF.id]: e.target.value
                                        })
                                    }
                                />
                            </div>
                        ))}
                        <Typography style={{ fontSize: 22 }}>
                            ¿Cómo te gustaría participar?
                        </Typography>
                        {preg.map((checks) => (
                            <div>
                                <Typography
                                    style={{ fontSize: isMobile ? 12 : 14 }}
                                >
                                    <Checkbox
                                        color="primary"
                                        checked={formu.ayuda === checks}
                                        onChange={() =>
                                            setFormu({
                                                ...formu,
                                                ayuda: checks
                                            })
                                        }
                                        inputProps={{
                                            'aria-label': 'primary checkbox'
                                        }}
                                    />
                                    {checks}
                                </Typography>
                            </div>
                        ))}
                    </div>

                    <Button
                        onClick={() => handlerSubmit()}
                        color="primary"
                        variant="contained"
                        disabled={!validateForm()}
                    >
                        ENVIAR
                    </Button>
                </Paper>
            </div>

            <div style={{ height: '12vh' }}>
                <img
                    alt="jorgelina mudallel"
                    style={{ height: isMobile ? 60 : 80, margin: 10 }}
                    src={hsf}
                />
            </div>
        </GoogleReCaptchaProvider>
    );
};

const textFi = [
    { id: 'nombre', name: 'Nombre', type: 'text' },
    { id: 'apellido', name: 'Apellido', type: 'text' },
    { id: 'dni', name: 'DNI', type: 'number' },
    { id: 'tel', name: 'Numero de teléfono  móvil', type: 'number' },
    { id: 'email', name: 'Correo electrónico', type: 'email' }
];

const preg = [
    'En Redes Sociales (Voluntario Digital)',
    'Recorriendo los Barrios',
    'Armando Reuniones en mi casa',
    'Llamando a otras personas a sumarse',
    'Siendo Fiscal',
    'Colaborar con alimentos o vestimenta'
];

export default FormuPart;
