import React, { useState } from 'react';
import { makeStyles, IconButton, Zoom } from '@material-ui/core';
import Fab from '@material-ui/core/Fab';
import MenuIcon from '@material-ui/icons/Menu';
import Drawer from '@material-ui/core/Drawer';
import CloseIcon from '@material-ui/icons/Close';
import colores from '../styles/colors';
import NestedList from './menuList';
import jorLogo from '../assets/jorLogo.png'

const MobileMenu = () => {
    const classes = useStyles();
    const [openMenu, setOpenMenu] = useState(false);

    return (
        <>
            <Zoom in={true} timeout={1100}>
                <Fab onClick={() => setOpenMenu(true)}>
                    <MenuIcon />
                </Fab>
            </Zoom>
            <Drawer
                classes={{ paper: classes.drawer }}
                anchor="left"
                open={openMenu}
                onClose={() => setOpenMenu(false)}
            >
                <div className={classes.content}>
                    <IconButton
                        className={classes.exitBtn}
                        edge="end"
                        color={colores.mainDarkfull}
                        onClick={() => setOpenMenu(false)}
                        aria-label="close"
                    >
                        <CloseIcon />
                    </IconButton>
                    <div style={{ height: 85, marginLeft: 25, marginTop: 5, marginBottom: 20 }} >
                        <img src={jorLogo} alt="jorLogo" style={{height: '100%'}} />
                    </div>

                    <NestedList />
                </div>
            </Drawer>
        </>
    );
};

const useStyles = makeStyles((theme) => ({
    drawer: {
        backgroundColor: '#f2f2f2'
    },
    content: {},
    exitBtn: { backgroundColor: colores.mainDark, margin: 20 }
}));

export default MobileMenu;
