import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import CardNoticia from '../components/cardNoticia';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { FormContext } from '../context/formContext';
import {
    makeStyles,
    IconButton,
    Typography,
    CardMedia,
    Card,
    Chip
} from '@material-ui/core';
import colores from '../styles/colors';
import React, { useEffect, useContext, useRef, useState } from 'react';
import { isMobile } from 'react-device-detect';

const NoticiasModal = ({ open, setOpen }) => {
    const { noticias } = useContext(FormContext);
    const [clickNota, setClickNota] = useState(false);
    const classes = useStyles(clickNota);

    const descriptionElementRef = useRef(null);
    useEffect(() => {
        if (open) {
            const { current: descriptionElement } = descriptionElementRef;
            if (descriptionElement !== null) {
                descriptionElement.focus();
            }
        }
    }, [open]);

    return (
        <Dialog
            maxWidth="md"
            fullScreen={isMobile}
            open={open}
            onClose={() => setOpen(false)}
            scroll={'paper'}
        >
            <DialogTitle
                id="prensa"
                style={{ backgroundColor: clickNota && clickNota.color }}
            >
                {clickNota ? (
                    <>
                        <IconButton
                            edge="start"
                            color={colores.mainDarkfull}
                            onClick={() => setClickNota(false)}
                            aria-label="close"
                            size="small"
                        >
                            <ArrowBackIcon />
                        </IconButton>
                        <Typography className={classes.title}>
                            {clickNota.title}
                        </Typography>
                    </>
                ) : (
                    <>
                        <IconButton
                            edge="start"
                            color={colores.mainDarkfull}
                            onClick={() => setOpen(false)}
                            aria-label="close"
                        >
                            <ArrowBackIcon />
                        </IconButton>
                        {clickNota.title}
                        Ultimas Noticias
                    </>
                )}
            </DialogTitle>
            <DialogContent dividers={true}>
                {!clickNota ? (
                    <div className={classes.modalCont}>
                        {noticias.map((nota) => (
                            <CardNoticia
                                data={nota}
                                key={nota.title}
                                setClickNota={setClickNota}
                            />
                        ))}
                    </div>
                ) : (
                    <NoticiaOn data={clickNota} />
                )}
            </DialogContent>
            <DialogActions
                style={{ backgroundColor: clickNota && clickNota.color }}
            >
                <div
                    style={{
                        display: 'flex',
                        height: 20,
                        overflowX: 'auto'
                    }}
                >
                    {/*   {temasChip.map((tema, i) => (
                        <Chip
                            key={i}
                            style={{ marginRight: 10, cursor: 'pointer' }}
                            label={tema}
                            variant="outlined"
                            color="primary"
                        />
                    ))} */}
                </div>
            </DialogActions>
        </Dialog>
    );
};

const NoticiaOn = ({ data }) => {
    const classes = useStyles();

    return (
        <div className={classes.modalContNota}>
            <Typography className={classes.sub}>{data.subtitle}</Typography>
            <Card elevation={3} className={classes.media}>
                <CardMedia image={data.img} style={{ height: '100%' }} />
            </Card>
            <Typography className={classes.text}>{data.text}</Typography>
        </div>
    );
};

const temasChip = ['Todo', 'ambiente', 'genero', 'barrios', 'covid'];

const useStyles = makeStyles(() => ({
    modalCont: {
        paddingRight: isMobile ? null : '10%',
        paddingLeft: isMobile ? null : '10%',
        '*::-webkit-scrollbar': {
            width: '0.3em'
        },

        '::-webkit-scrollbar-track': {
            background: 'transparent'
        },

        '::-webkit-scrollbar-thumb': {
            backgroundColor: 'grey',
            borderRadius: 15
        }
    },
    modalContNota: {
        paddingLeft: !isMobile && 25,
        paddingRight: !isMobile && 25,
        '*::-webkit-scrollbar': {
            width: '0.3em'
        },

        '::-webkit-scrollbar-track': {
            background: 'transparent'
        },

        '::-webkit-scrollbar-thumb': {
            backgroundColor: 'grey',
            borderRadius: 15
        }
    },
    media: {
        height: isMobile ? 220 : 320,
        marginTop: 20,
        marginBottom: 20,
        borderRadius: 5
    },
    title: {
        marginTop: !isMobile && 5,
        fontSize: isMobile ? 16 : 22,
        letterSpacing: 1.2
    },
    sub: {
        fontWeight: 'bold',
        fontSize: isMobile ? 14 : 18,
        letterSpacing: !isMobile && 2
    },
    text: {
        fontSize: isMobile ? 14 : 16,
        letterSpacing: 1.4
    }
}));

export default NoticiasModal;
