import React, { useState } from 'react';
import { isMobile } from 'react-device-detect';
import { makeStyles, Grow, Fade } from '@material-ui/core';
import Fab from '@material-ui/core/Fab';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Avatar from '@material-ui/core/Avatar';
import EcoIcon from '@material-ui/icons/Eco';
import EqualizerIcon from '@material-ui/icons/Equalizer';
import fem from '../assets/iconos/fem.png';
import colores from '../styles/colors';
import Tooltip from '@material-ui/core/Tooltip';
import FingerprintIcon from '@material-ui/icons/Fingerprint';

const PaperText = () => {
    const styles = useStyles();

    const [ejeType, setEjeType] = useState('info');

    return (
        <div>
            <Paper elevation={3} className={styles.paperDesc2}>
                <TextEffect active={ejeType} />
            </Paper>
            <Grow in={true} timeout={600}>
                <div className={styles.ejesButton}>
                    {ejesCamp.map((eje, i) => {
                        return (
                            <Tooltip
                                key={i}
                                TransitionComponent={Fade}
                                title={eje.name}
                            >
                                <Fab
                                    style={{
                                        backgroundColor: eje.color,
                                        height: isMobile ? 40 : null,
                                        width: isMobile ? 40 : null
                                    }}
                                    aria-label={eje.name}
                                    size="medium"
                                    onClick={() => {
                                        setEjeType(eje.key);
                                    }}
                                >
                                    {eje.icon}
                                </Fab>
                            </Tooltip>
                        );
                    })}
                </div>
            </Grow>
        </div>
    );
};

const TextEffect = ({ active }) => {
    const classes = useStyles();
    return (
        <div>
            {Object.keys(texts).map((key) => {
                if (key === active) {
                    return (
                        <Grow key={key} in={true} timeout={600}>
                            <Typography className={classes.textP}>
                                {texts[key]}
                            </Typography>
                        </Grow>
                    );
                }
            })}
        </div>
    );
};

const ejesCamp = [
    {
        key: 'info',
        icon: <FingerprintIcon />,
        name: 'info',
        color: colores.letrajor08
    },
    {
        key: 'ambiente',
        icon: <EcoIcon />,
        name: 'Medio Ambiente',
        color: colores.ejesGreen
    },
    {
        key: 'produccion',
        icon: <EqualizerIcon />,
        name: 'produccion',
        color: colores.ejesOrange
    },
    {
        key: 'genero',
        icon: <Avatar src={fem} style={{ height: 24, width: 24 }} />,
        name: 'Género y Diversidad',
        color: colores.ejesPurple
    }
];

const texts = {
    info: `Soy santafesina, nací y crecí en esta ciudad que amo. Siento un gran compromiso con los vecinos y vecinas de Santa Fe, con los barrios, con las mujeres, las diversidades y las juventudes. Trabajo todos los días para construir una ciudad con potencia productiva, inclusiva, sustentable y con la capacidad transformadora necesaria para generar oportunidades para todos y todas. `,
    ambiente: `Trabajo a través de diferentes proyectos y campañas en la construcción de una conciencia colectiva en materia de reciclaje, reutilización y cuidado de nuestro ambiente, 
		como eje fundamental para el desarrollo sustentable de nuestra ciudad.`,
    produccion: `Trabajamos en la sostenibilidad, fortalecimiento y desarrollo de emprendimientos, tanto individuales como asociativos, contemplando las características económicas, sociales, culturales y ambientales de los mismos. Consideramos fundamental que el Municipio brinde herramientas de asistencia técnica, acompañamiento, capacitación y financiamiento. En este sentido, 
    impulsamos el desarrollo e implementación de políticas públicas que posibiliten el abordaje de las temáticas.`,
    genero: 'Desde mi lugar, formo parte de la lucha por el empoderamiento e igualdad de oportunidades para mujeres e identidades disidentes de nuestra ciudad'
};

const useStyles = makeStyles((theme) => ({
    paperDesc2: {
        marginRight: isMobile ? 3 : null,
        marginLeft: isMobile ? 3 : null,
        height: isMobile ? 170 : 240,
        overflow: 'auto',
        padding: isMobile ? 15 : 25,
        maxWidth: 480,
        borderRadius: 15,
        backgroundColor: 'rgba(228, 235, 241, 0.8)',
        color: colores.gamBdark,
        textAlign: isMobile ? 'center' : null, //'justify',
        '&::-webkit-scrollbar': {
            width: '0em'
        }
    },
    ejesButton: {
        marginTop: isMobile ? -18 : -24,
        width: isMobile ? '65%' : '50%',
        marginRight: 'auto',
        marginLeft: 'auto',
        display: 'flex',
        justifyContent: 'space-between'
    },
    iconSize: { height: isMobile ? 22 : 28, width: isMobile ? 22 : 28 },
    textP: {
        fontSize: isMobile ? 16 : 18,
        letterSpacing: 1.2
    }
}));

export default PaperText;

/*  info: `me llamo Jorgelina Mudallel, me dicen Jorita, naci y me crie en barrio roma, desde muy chica camino la ciudad y siempre senti la vocacion de ayudar al otro, de participar, involucrarme en un fuerte compromiso social.
decidi estudiar sociologia para comprender los problemas sociales y entender la sociedad en la que vivo, pero senti la necesidad de tener un rol mas comprometido con mi ciudad, 
entendi que la politica es la verdadera herramienta de transformacion de la realidad, por eso vengo a proponerles un proyecto que incluya a todas las voces,
formar una sociedad inclusiva, sustentable, productiva y con igualdad de oportunidades, una sociedad en la que todos y todas podamos crecer.`, */
