import {
    CardMedia,
    Grid,
    Typography,
    Card,
    CardContent,
    makeStyles
} from '@material-ui/core';
import { isMobile } from 'react-device-detect';
import ciudadsf from '../../assets/city/ciudadsf.jpg';
import ruta11 from '../../assets/city/ruta11.png';
import ciclovia from '../../assets/city/ciclovia.jpg';
import fumigacion from '../../assets/city/fumigacion.jpeg';
import transporte from '../../assets/city/sustentable.jpg';
import colores from '../../styles/colors';
import Loader from '../loader';
import useProgressiveImage from '../../helpers/progressiveImg';

const CityContent = ({ data }) => {
    const classes = useStyles();
    const imag = useProgressiveImage(ciudadsf);

    if (!imag) {
        return <Loader />;
    }
    return (
        <div className={classes.marginContainer}>
            <Grid container spacing={5}>
                <Grid item xs={12} sm={6}>
                    <Card elevation={3} className={classes.cardImgPr}>
                        <CardMedia
                            image={imag}
                            style={{
                                height: '100%',
                                borderRadius: 15
                            }}
                        />
                    </Card>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <Typography className={classes.typograf}>
                        Las ciudades actuales tienen situaciones que son un gran
                        desafío para el ambiente, y está comprobado que la
                        calidad de vida dentro de las ciudades es contradictoria
                        y desigual. Es necesario integrar parámetros ambientales
                        en la planificación y gestión de nuestra ciudad para
                        lograr que tenga un funcionamiento más sostenible. En
                        este sentido, Santa Fe debe transformar sus formas y
                        funcionalidades urbanas para alcanzar una buena gestión
                        territorial. El desarrollo de la ciudad que queremos
                        contiene de manera implícita aspectos de sustentabilidad
                        en todas sus acciones, para lograr una administración
                        eficiente y racional de los bienes naturales en el largo
                        plazo, bajo los principios de la cohesión económica,
                        social y territorial, protegiendo el ambiente y
                        respetando nuestra diversidad cultural. Trabajamos en
                        proyectos que transformen nuestra ciudad en la capital
                        que soñamos, una Santa Fe sustentable, productiva,
                        dinámica, abierta y segura.
                    </Typography>
                </Grid>
                <Grid item xs={12} sm={12}>
                    <Typography style={{ textAlign: 'center', fontSize: 22 }}>
                        Mejora en espacios publicos
                    </Typography>
                </Grid>
                {dataCardL.map((card) => (
                    <Grid item xs={12} sm={4}>
                        <CardL data={card} />
                    </Grid>
                ))}
                <Grid item xs={12} sm={12}>
                    <Typography style={{ textAlign: 'center', fontSize: 22 }}>
                        Movilidad sustentable y segura
                    </Typography>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <Typography className={classes.typograf}>
                        Es necesario trabajar en una red de transporte integrado
                        o que combine distintos tipos de movilidad para llegar a
                        destino También, debemos propiciar la sustentabilidad
                        económica, social y ambiental del sistema de transporte,
                        previendo tiempos y costos razonables en la movilidad y
                        el menor impacto respecto del entorno. La bicicleta ha
                        demostrado ser una de las mejores opciones para apoyar
                        la construcción de un ambiente sano y amigable, que
                        aporte al desarrollo sostenible y que mejore el uso y
                        apreciación del espacio público por parte de los
                        ciudadanos.{' '}
                    </Typography>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <Card
                        elevation={3}
                        style={{ height: 260, borderRadius: 15, padding: 3 }}
                    >
                        <CardMedia
                            image={transporte}
                            style={{
                                height: '100%',
                                borderRadius: 15
                            }}
                        />
                    </Card>
                </Grid>
                <Grid item xs={12} sm={4}>
                    <Card elevation={3} className={classes.cardPadd}>
                        <CardContent>
                            <Typography className={classes.typograf}>
                                Fortalecimiento del programa “Subite a la Bici”,
                                para que esté disponible en todos los Distritos
                                de la Ciudad, y estableciendo que la cantidad de
                                bicicletas disponibles guarde una relación a la
                                cantidad de habitantes por distrito. También,
                                hemos trabajado un proyecto que tiende a darle
                                el marco legal correspondiente a este programa.
                                Consideramos sumamente necesario alentar el uso
                                de la bicicleta, con políticas claras y precisas
                                sobre cómo hacerlo y qué recursos se destinan
                                para ello.
                            </Typography>
                        </CardContent>
                    </Card>
                </Grid>
                <Grid item xs={12} sm={4}>
                    <Card elevation={3} className={classes.cardPadd}>
                        <CardContent>
                            <Typography className={classes.typograf}>
                                Adhesión en todos sus términos a la Ley
                                Provincial N° 13.781, que regula el fomento de
                                la industria de vehículos eléctricos y
                                tecnologías de energías alternativas para la
                                movilidad urbana y periurbana. Esto abarca tanto
                                los vehículos de uso particular o profesional,
                                agrícola, transporte de carga y público de
                                pasajeros.
                            </Typography>
                        </CardContent>
                    </Card>
                </Grid>

                <Grid item xs={12} sm={4}>
                    <Card elevation={3} className={classes.cardPadd}>
                        <CardContent>
                            <Typography className={classes.typograf}>
                                Solicitamos que se declare como política pública
                                municipal de la ciudad de Santa Fe la progresiva
                                incorporación y uso de vehículos eléctricos y
                                con tecnologías de energías alternativas, como
                                medio para contribuir a la reducción de la
                                contaminación ambiental.
                            </Typography>
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>
        </div>
    );
};

const CardL = ({ data }) => {
    const classes = useStyles();
    return (
        <Card elevation={3} className={classes.cardMed}>
            <CardMedia image={data.img} className={classes.mediaico} />
            <CardContent>
                <Typography className={classes.typograf}>
                    {data.text}
                </Typography>
            </CardContent>
        </Card>
    );
};

const dataCardL = [
    {
        id: 0,
        title: 'card1',
        text: 'Mantenimiento y/o reposición de la luminaria de la Ruta Nacional Nº 11 (Av. de Circunvalación Oeste – Jurisdicción de la ciudad de Santa Fe) sobre el Tramo: Rulo de CILSA – Acc. Hipódromo.',
        ico: '',
        img: ruta11
    },
    {
        id: 1,
        title: 'card2',
        text: 'Refacción y puesta en funcionamiento de las totalidad de luminarias existentes en la Ciclovía con trazado paralelo a calle Pedro Vittori, denominada “Intendente Noé Adán Campagnolo”; y en la Ciclovía que se encuentra paralela a calle Vélez Sarsfield, la cual se denomina “Arquitecto Alberto Hammerly" ',
        ico: '',
        img: ciclovia
    },
    {
        id: 2,
        title: 'card3',
        text: 'Baños químicos en la Feria la Baulera los días jueves y sábados. Tambien fumigación en barrios plazas y playas por mosquito Aeades Aegypti',
        ico: '',
        img: fumigacion
    }
];

const useStyles = makeStyles((theme) => ({
    marginContainer: {
        width: isMobile && '94%',
        margin: isMobile && 'auto'
    },
    typograf: {
        textAlign: isMobile && 'center',
        color: colores.darkBlueNav,
        fontSize: 16
    },
    cardImgPr: {
        height: isMobile ? 200 : 300,
        borderRadius: 15,
        padding: 3
    },
    cardPadd: {
        height: !isMobile && 280,
        padding: '10px 20px',
        overflow: 'auto'
    },
    cardMed: {
        height: !isMobile && 350,
        borderRadius: 15
    },
    mediaico: {
        height: 120
    }
}));

export default CityContent;
