import { Box, Typography, Avatar } from '@material-ui/core';
import { isMobile } from 'react-device-detect';
import VerticalTabs from '../verticalTabs';
import PropTypes from 'prop-types';
import Beg from '.././provContent/beg2';
import Billetera from '.././provContent/billetera';
import Vacunas from '.././provContent/vacunas';
import Incluir from '.././provContent/incluir';
import begIcon from '../../assets/ejesProv/begicon.jpg';
import billetera from '../../assets/ejesProv/bille.png';
import vacuna from '../../assets/ejesProv/vacSF.png';
import incluir from '../../assets/ejesProv/incluirLogo.png';

const ProvinciaContent = ({ val }) => {
    return (
        <>
            {!isMobile && <VerticalTabs />}
            {isMobile &&
                provBtn.map((tab, i) => (
                    <TabPanel key={i} value={val} index={i}>
                        {tab.tabContent}
                    </TabPanel>
                ))}
        </>
    );
};

const provBtn = [
    {
        id: 'Billetera',
        tabContent: <Billetera />,
        img: billetera,
        title: 'Billetera Santa Fe',
        ico: <Avatar src={billetera} style={{ height: 38, width: 38 }} />
    },
    {
        id: 'BEG',
        tabContent: <Beg />,
        img: begIcon,
        title: 'Boleto Educativo Gratuito',
        ico: <Avatar src={begIcon} style={{ height: 38, width: 38 }} />
    },

    {
        id: 'Incluir',
        tabContent: <Incluir />,
        img: incluir,
        title: 'Programa Incluir',
        ico: <Avatar src={incluir} style={{ height: 38, width: 38 }} />
    },
    {
        id: 'Plan Provincial Santa Fe Vacuna contra el Covid',
        tabContent: <Vacunas />,
        img: vacuna,
        ico: <Avatar src={vacuna} style={{ height: 38, width: 38 }} />
    }
];

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`scrollable-force-tabpanel-${index}`}
            aria-labelledby={`scrollable-force-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={0}>
                    <Typography style={{ textAlign: 'center' }}>
                        {children}
                    </Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired
};

export default ProvinciaContent;
