import React from 'react';
import { ThemeProvider, makeStyles } from '@material-ui/core/styles';
import { Fab, Typography } from '@material-ui/core';
import QuestionAnswerIcon from '@material-ui/icons/QuestionAnswer';
import colores from '../styles/colors';
import { isMobile } from 'react-device-detect';

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.background,
        height: !isMobile && 110,
        width: '100%',
        borderRadius: 15,
        border: 0,
        overflow: 'auto',
        marginBottom: 15,
        padding: isMobile ? 20 : 16,
        backgroundColor: '#0472c2',
        color: '#f2f2f2',
        cursor: 'pointer',
        boxShadow: '8px 8px 14px -8px rgba(0,0,0,0.45)',
        transition: `${theme.transitions.create(['boxShadow', 'transform'])}`,
        '&:hover': {
            boxShadow: '8px 8px 14px -2px rgba(0,0,0,0.45)',
            transform: 'scale(1.02)'
        }
    },
    icon: {
        fontSize: 30,
        color: theme.background
    }
}));

function DeepChild({ ico, txt1, txt2, txt3 }) {
    const classes = useStyles();

    return (
        <button type="button" className={classes.root}>
            {ico}
            <Typography
                style={{
                    fontSize: 24,
                    marginBottom: '10%'
                }}
            >
                {txt1}
            </Typography>

            <Typography style={{ fontSize: 22, marginTop: '10%' }}>
                {txt2}
            </Typography>
        </button>
    );
}

const themeBlue = {
    background:
        'linear-gradient(to right top, #76ddff, #62d2f9, #4cc6f3, #32bbee, #00afe8)'
};

export default function BtnGradiantJor({ ico, txt1, txt2, txt3 }) {
    return (
        <ThemeProvider theme={themeBlue}>
            <DeepChild ico={ico} txt1={txt1} txt2={txt2} txt3={txt3} />
        </ThemeProvider>
    );
}
