import {
    Card,
    CardActionArea,
    CardMedia,
    Divider,
    Grid,
    Grow,
    makeStyles,
    Typography
} from '@material-ui/core';
import { useState } from 'react';
import { isMobile } from 'react-device-detect';
import EjesContent from '../../custom/ejesContent';
import colores from '../../styles/colors';
import ejesData from '../../info/ejes';

const ContentEjes = () => {
    const [swCont, setSwCont] = useState('ambiente');

    const [dataEje, setDataEje] = useState(ejesData[0]);
    return (
        <>
            <CardsContent
                setSwCont={setSwCont}
                swCont={swCont}
                setDataEje={setDataEje}
            />
            <Divider variant="middle" style={{ marginTop: 15 }} />

            <Typography
                style={{
                    fontSize: 24,
                    letterSpacing: 2.6,
                    textAlign: 'center',
                    color: dataEje.letra
                }}
            >
                {dataEje.title}
            </Typography>

            <Divider variant="middle" style={{ marginBottom: 15 }} />

            <EjesContent data={dataEje} />
        </>
    );
};

const CardsContent = ({ setSwCont, swCont, setDataEje }) => {
    return (
        <Grid
            container
            spacing={isMobile ? 1 : 4}
            style={{
                width: isMobile ? '100%' : '90%',
                marginRight: 'auto',
                marginLeft: 'auto'
            }}
        >
            {ejesData.map((eje, i) => (
                <Grow
                    key={eje.id}
                    in={true}
                    mountOnEnter
                    timeout={400 * i + 400}
                >
                    <Grid item xs={3} sm={3}>
                        <CardEjes
                            eje={eje}
                            setSwCont={setSwCont}
                            swCont={swCont}
                            setDataEje={setDataEje}
                        />
                    </Grid>
                </Grow>
            ))}
        </Grid>
    );
};

const CardEjes = ({ eje, setSwCont, swCont, setDataEje }) => {
    const classes = useStyles();
    return (
        <Card
            elevation={swCont === eje.id ? 1 : 5}
            style={{
                maxWidth: 250,
                padding: isMobile ? 2 : 0,
                marginLeft:
                    eje.id === 'ambiente' || (eje.id === 'produccion' && -4),
                borderRadius: isMobile ? '15%' : 15,
                textAlign: 'center',
                color: swCont !== eje.id && eje.color,
                backgroundColor:
                    swCont === eje.id ? eje.color : colores.mainLight
            }}
            onClick={() => {
                setSwCont(eje.id);
                setDataEje(eje);
            }}
        >
            <CardActionArea>
                {isMobile ? (
                    eje.ico
                ) : (
                    <CardMedia
                        style={{
                            height: 75,
                            marginTop: 5,
                            marginRight: 5,
                            marginLeft: 5,
                            borderRadius: 15
                        }}
                        image={eje.img}
                        title={eje.title}
                    />
                )}
                <Typography
                    gutterBottom
                    variant="h5"
                    style={{
                        fontSize: isMobile ? 12 : 16,
                        marginTop: 5,
                        color: eje.letra
                    }}
                >
                    {eje.name}
                </Typography>
            </CardActionArea>
        </Card>
    );
};

const useStyles = makeStyles((theme) => ({}));

export default ContentEjes;
