import React, { useState } from 'react';

import { makeStyles } from '@material-ui/core/styles';
import ListSubheader from '@material-ui/core/ListSubheader';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Avatar from '@material-ui/core/Avatar';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import ShareIcon from '@material-ui/icons/Share';
import DashboardIcon from '@material-ui/icons/Dashboard';
import BookmarksIcon from '@material-ui/icons/Bookmarks';
import FacebookIcon from '@material-ui/icons/Facebook';
import YouTubeIcon from '@material-ui/icons/YouTube';
import TwitterIcon from '@material-ui/icons/Twitter';
import InstagramIcon from '@material-ui/icons/Instagram';
import hsf from '../assets/ejesProv/hsf2.jpg';
import ModalContent from '../pages/ModalContent';
import ApartmentIcon from '@material-ui/icons/Apartment';
import BeenhereIcon from '@material-ui/icons/Beenhere';
import ArtTrackIcon from '@material-ui/icons/ArtTrack';
import colores from '../styles/colors';
import NoticiasModal from '../pages/noticiasModal';



const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        maxWidth: 280,
        overflow: 'auto',
        scrollbarWidth: 0
    },
    nested: {
        paddingLeft: theme.spacing(4)
    },
    listItems: {
        height: '80%',
        overflow: 'auto',
        scrollbarWidth: 0
    }
}));

export default function NestedList() {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const [modalHandler, setModalHandler] = useState(false);
    const [modalData, setModalData] = useState({});
    const [openNoticias, setOpenNoticias] = useState(false);

    const handleClick = () => {
        setOpen(!open);
    };

    function handlerModal(data) {
        
       
        if (data.func) return data.func();
        setModalData(data);
        setModalHandler(true);
    }

    const mainButtons = [
        {
            id: 'hacemos',
            icon: <Avatar src={hsf} style={{marginLeft: -5}} />,
            name: 'Hacemos Santa Fe',
            func: () =>
                window.open('https://www.jorgelinamudallel.com.ar/sumate')
        },
        {
            id: 'prov',
            icon: <BeenhereIcon style={{ color: colores.darkBlueNav }} />,
            name: 'Gobierno Presente',
            func: ''
        },
        {
            id: 'ejes',
            icon: <DashboardIcon style={{ color: colores.darkBlueNav }} />,
            name: `Ejes de Trabajo`
        },
        {
            id: 'proyectos',
            icon: <BookmarksIcon style={{ color: colores.darkBlueNav }} />,
            name: 'Proyectos en el Concejo'
        },
        {
            id: 'ciudad',
            icon: <ApartmentIcon style={{ color: colores.darkBlueNav }} />,
            name: 'La Ciudad que Queremos'
        },
        {
            id: 'prensa',
            icon: <ArtTrackIcon style={{ color: colores.darkBlueNav }} />,
            name: 'Prensa',
            func: () => setOpenNoticias(true)
        },
        {
            id: 'redes',
            icon: <ShareIcon style={{ color: colores.darkBlueNav }} />,
            name: 'Redes Sociales',
            func: () => handleClick()
        }
    ];

    const redesItem = [
        {
            name: 'Facebook',
            icon: <FacebookIcon style={{ color: colores.darkBlueNav }} />,
            color: '#3b5998',
            url: 'https://www.facebook.com/jorgelinamudallel/'
        },
        {
            name: 'Instagram',
            icon: <InstagramIcon style={{ color: colores.darkBlueNav }} />,
            color: '#d11563',
            url: 'https://www.instagram.com/jormudallel/'
        },
        {
            name: 'Twitter',
            icon: <TwitterIcon style={{ color: colores.darkBlueNav }} />,
            color: '#00aced',
            url: 'https://twitter.com/JoritaMudallel'
        },
        {
            name: 'YouTube',
            icon: <YouTubeIcon style={{ color: colores.darkBlueNav }} />,
            color: ' #bb0000',
            url: 'https://www.youtube.com/channel/UCruszoz-TRFXYXr0SWXScfg'
        }
    ];

    return (
        <>
            <List
                component="nav"
                aria-labelledby="menumobile"
               /*  subheader={
                    <ListSubheader component="div" id="nested-list-subheader">
                        Jorgelina Mudallel
                    </ListSubheader>
                } */
                className={classes.root}
            >
                {mainButtons.map((item, i) => (
                    <ListItem
                        key={i + 10}
                        button
                        onClick={() => handlerModal(item)}
                        style={{ marginBottom: 15 }}
                    >
                        <ListItemIcon>{item.icon}</ListItemIcon>
                        {item.id !== 'redes' ? (
                            <ListItemText primary={item.name} />
                        ) : (
                            <>
                                <ListItemText primary={item.name} />
                                {open ? <ExpandLess /> : <ExpandMore />}
                            </>
                        )}
                    </ListItem>
                ))}

                <Collapse in={open} timeout="auto" unmountOnExit>
                    <List component="div" disablePadding>
                        {redesItem.map((item, i) => (
                            <ListItem
                                key={i}
                                button
                                className={classes.nested}
                                onClick={() => {
                                    Event("PRODUCT", "Product added to cart", "PRODUCT_PAGE")
                                    window.open(item.url);
                                }}
                            >
                                <ListItemIcon>{item.icon}</ListItemIcon>
                                <ListItemText primary={item.name} />
                            </ListItem>
                        ))}
                    </List>
                </Collapse>
            </List>
            <NoticiasModal open={openNoticias} setOpen={setOpenNoticias} />
            <ModalContent
                data={modalData}
                tabi={modalData.id}
                open={modalHandler}
                handleClose={() => {
                    setModalHandler(false);
                }}
            />
        </>
    );
}
