import { makeStyles } from '@material-ui/core';
import { isMobile } from 'react-device-detect';
import colores from './colors';

const provStyles = makeStyles((theme) => ({
    marginContainer: {
        width: isMobile && '94%',
        margin: isMobile && 'auto'
    },
    icon: {
        height: 26,
        width: 26,
        marginRight: 10
    },
    cardCenter: {
        height: isMobile ? 120 : 140,
        padding: isMobile ? 10 : '18px 14px',
        background: theme.backgroundJor,
        overflow: 'auto',
        //letterSpacing: 1.2,
        textAlign: 'center',
        borderRadius: 15,
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        color: colores.darkBlueNav
    },
    cards: {
        height: !isMobile && 200,
        padding: isMobile ? 10 : 20,
        background: theme.backgroundJor,
        overflow: 'auto',
        letterSpacing: 1.4,
        textAlign: isMobile && 'center',
        borderRadius: 15,
        color: colores.darkBlueNav
    },
    cardMini: {
        display: isMobile && 'flex',
        alignItems: 'center',
        height: isMobile ? 150 : 120,
        padding: isMobile ? 10 : 18,
        background: theme.backgroundJor,
        overflow: 'auto',
        letterSpacing: 1.4,
        textAlign: isMobile && 'center',
        borderRadius: 15,
        color: colores.darkBlueNav
    },
    cardVert: {
        height: !isMobile && 110,
        borderRadius: 15,
        overflow: 'auto',
        marginBottom: 15,
        padding: isMobile ? 20 : 16,
        background: theme.backgroundBlueDark,
        color: '#f2f2f2',
        cursor: 'pointer',
        transition: `${theme.transitions.create(['transform'])}`,
        '&:hover': {
            transform: 'scale(1.02)'
        }
    },
    textGrl: {
        fontSize: isMobile ? 14 : 22
    },
    textBig: {
        fontSize: isMobile ? 22 : 24,
        letterSpacing: 1.2,
        marginBottom: !isMobile && 3
    },
    textmed: {
        fontSize: isMobile ? 12 : 14,
        letterSpacing: 1.2
    },
    textTitle: {
        fontSize: isMobile ? 16 : 18,
        letterSpacing: 1
    },
    media: {
        cursor: 'pointer',
        height: 91,
        borderRadius: 15,
        marginBottom: 16
    },
    gradiantBlue: {
        backgroundColor:
            'linear-gradient(to right top, #23467a, #04689d, #008cbb, #00b0d2, #1ad5e3)'
    },
    '@global': {
        '*::-webkit-scrollbar': {
            width: '0em'
        }
    }
}));

export default provStyles;
