import React from 'react';
import { ThemeProvider, makeStyles } from '@material-ui/core/styles';
import { Fab, Typography } from '@material-ui/core';
import QuestionAnswerIcon from '@material-ui/icons/QuestionAnswer';
import colores from '../styles/colors';

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.background,
        borderWidth: 0,
        borderRadius: 15,
        boxShadow: '8px 8px 14px -6px rgba(0,0,0,0.45)',
        color: colores.gamBdark,
        height: '100%',
        padding: '0 30px',
        cursor: 'pointer',
        animation: `3000ms ${theme.transitions.easing}`,
        transition: `${theme.transitions.create(['boxShadow', 'transform'])}`,
        '&:hover': {
            boxShadow: '8px 8px 14px -2px rgba(0,0,0,0.45)',
            transform: 'scale(1.02)'
        }
    },
    icon: {
        fontSize: 30,
        color: theme.background
    }
}));

function DeepChild({ ico, txt1, txt2, txt3 }) {
    const classes = useStyles();

    return (
        <button type="button" className={classes.root}>
            <Typography
                style={{
                    fontSize: 24,
                    marginBottom: '10%'
                }}
            >
                {txt1}
            </Typography>

            <QuestionAnswerIcon className={classes.icon} />

            <Typography style={{ fontSize: 22, marginTop: '10%' }}>
                {txt2}
            </Typography>
            <Typography style={{ fontSize: 12, marginTop: '5%' }}>
                {txt3}
            </Typography>
        </button>
    );
}

const themeBlue = {
    background:
        'linear-gradient(to right top, #b1e7bd, #90da9e, #6fcc7e, #4bbe5d, #15b038)'
    //'linear-gradient(to right top,#845EC2,#D65DB1 ,#FF6F91,#FF9671,#FFC75F,#F9F871  )'
    // default 'linear-gradient(to right top, #d16ba5, #c777b9, #ba83ca, #aa8fd8, #9a9ae1, #8aa7ec, #79b3f4, #69bff8, #52cffe, #41dfff, #46eefa, #5ffbf1)'
    // blue 'linear-gradient(to right top, #23467a, #04689d, #008cbb, #00b0d2, #1ad5e3)'
};

export default function BtnGradiantBlue({ ico, txt1, txt2, txt3 }) {
    return (
        <ThemeProvider theme={themeBlue}>
            <DeepChild ico={ico} txt1={txt1} txt2={txt2} txt3={txt3} />
        </ThemeProvider>
    );
}
