import React, { useContext, useState } from 'react';
import { isMobile } from 'react-device-detect';
import { Dialog, DialogTitle, Fab, Grid, Grow, Zoom } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';

import colores from '../../styles/colors';
import jorA from '../../assets/jor/jorConcejo.jpg';
import AccountBalanceIcon from '@material-ui/icons/AccountBalance';
import BeenhereIcon from '@material-ui/icons/Beenhere';
import BusinessIcon from '@material-ui/icons/Business';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import { FormContext } from '../../context/formContext';
import CardProyect from '../cardProyect';

const ProyectContent = () => {
    const { proyectos } = useContext(FormContext);
    const [openProyect, setOpenProyect] = useState(false);
    const windowWidth = window.innerWidth;

    return (
        <div
            style={{
                width: isMobile && '94%',
                margin: isMobile && 'auto'
            }}
        >
            <Grid container spacing={3} justify="center">
                <Grid item xs={12} sm={6}>
                    <Grow direction="up" in={true} mountOnEnter timeout={600}>
                        <Card
                            elevation={5}
                            style={{
                                borderRadius: 15,
                                height: isMobile ? 140 : '100%'
                            }}
                        >
                            <CardMedia
                                image={jorA}
                                style={{
                                    height: '100%'
                                }}
                            />
                        </Card>
                    </Grow>
                </Grid>

                <Grid item xs={12} sm={6}>
                    <Grid container spacing={2}>
                        {dataDash.map((dataCard, i) => (
                            <Zoom key={i} in={true} timeout={400}>
                                <Grid item xs={12} sm={6}>
                                    <CardDash {...dataCard} />
                                </Grid>
                            </Zoom>
                        ))}
                    </Grid>
                </Grid>

                {proyectos !== undefined &&
                    proyectos.map((proyect, i) => {
                        return (
                            <Grid
                                key={i}
                                item
                                xs={12}
                                sm={windowWidth > 1400 ? 3 : 4}
                            >
                                <CardProyect
                                    proyect={proyect}
                                    setOpenProyect={setOpenProyect}
                                />
                            </Grid>
                        );
                    })}
            </Grid>
            <Dialog
                onClose={() => setOpenProyect(false)}
                aria-labelledby="proyect dialog"
                open={openProyect}
                scroll="body"
            >
                <DialogTitle
                    id="simple-dialog-title"
                    style={{
                        backgroundColor: openProyect.color
                    }}
                >
                    <Typography style={{ fontSize: 22 }}>
                        {openProyect.title}
                    </Typography>
                    <Typography style={{ fontSize: 16 }}>
                        {openProyect.tipo}
                    </Typography>
                    <Typography style={{ fontSize: 14 }}>
                        {openProyect.eje}
                    </Typography>
                </DialogTitle>
                <div style={{ padding: 25 }}>
                    <Typography>{openProyect.text}</Typography>
                </div>
            </Dialog>
        </div>
    );
};

const CardDash = ({ title, total, icon, color, texColor, icoColor }) => {
    if (total === 0) {
        return (
            <Card
                elevation={3}
                style={{
                    height: '100%',
                    backgroundColor: color,
                    display: 'flex',
                    flexDirection: 'column-reverse',
                    borderRadius: 15
                }}
            >
                <CardContent
                    style={{
                        textAlign: 'center',
                        alignContent: 'space-between'
                    }}
                >
                    <Typography
                        style={{
                            fontSize: isMobile ? 20 : 22,
                            color: texColor
                        }}
                    >
                        {title}
                    </Typography>
                    <Fab
                        style={{
                            height: isMobile ? 50 : 60,
                            width: isMobile ? 50 : 60,
                            pointerEvents: 'none',
                            marginTop: 30,
                            backgroundColor: icoColor
                        }}
                    >
                        {icon}
                    </Fab>
                </CardContent>
            </Card>
        );
    }

    return (
        <Card
            elevation={3}
            style={{ backgroundColor: color, borderRadius: 15 }}
        >
            <CardContent style={{ textAlign: 'center' }}>
                <Fab
                    style={{
                        height: 45,
                        width: 45,
                        marginBottom: 10,
                        pointerEvents: 'none',
                        backgroundColor: icoColor
                    }}
                >
                    {' '}
                    {icon}
                </Fab>

                <Typography
                    style={{ fontSize: isMobile ? 40 : 40, color: texColor }}
                >
                    +{total}
                </Typography>
                <Typography
                    style={{ fontSize: isMobile ? 18 : 22, color: texColor }}
                >
                    {title}
                </Typography>
            </CardContent>
        </Card>
    );
};

const dataDash = [
    {
        title: 'Proyectos Presentados',
        total: 520,
        icon: (
            <AccountBalanceIcon
                style={{ color: colores.cardGrTex1 }}
                fontSize="medium"
            />
        ),
        color: colores.cardGradiant1,
        texColor: colores.cardGrTex1 /* , icoColor: colores.cardGrIco1 */
    },
    {
        title: 'Proyectos Aprobados',
        total: 282,
        icon: (
            <BeenhereIcon
                style={{ color: colores.cardGrTex2 }}
                fontSize="medium"
            />
        ),
        color: colores.cardGradiant2,
        texColor: colores.cardGrTex2 /* , icoColor: colores.cardGrIco2 */
    },
    {
        title: 'Sesiones Realizadas',
        total: 26,
        icon: (
            <BusinessIcon
                style={{ color: colores.cardGrTex3 }}
                fontSize="medium"
            />
        ),
        color: colores.cardGradiant3,
        texColor: colores.cardGrTex3 /* , icoColor: colores.cardGrIco3 */
    },
    {
        title: 'Conoce Mis Proyectos',
        total: 0,
        icon: (
            <ArrowDownwardIcon
                style={{ color: colores.cardGrTex4 }}
                fontSize="medium"
            />
        ),
        color: colores.cardGradiant4,
        texColor: colores.cardGrTex4 /* , icoColor: colores.cardGrIco4 */
    }
];

export default ProyectContent;
