import { useState } from 'react';

import { makeStyles, Typography } from '@material-ui/core';
import BookmarksIcon from '@material-ui/icons/Bookmarks';
import ModalContent from '../pages/ModalContent';
import ApartmentIcon from '@material-ui/icons/Apartment';
import DashboardIcon from '@material-ui/icons/Dashboard';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import colores from '../styles/colors';
import Fab from '@material-ui/core/Fab';
import Paper from '@material-ui/core/Paper';
import BeenhereIcon from '@material-ui/icons/Beenhere';
import { Slide } from '@material-ui/core';

const NavigationBar = () => {
    const classes = useStyles();
    const [value, setValue] = useState(null);
    const [modalHandler, setModalHandler] = useState(false);
    const [modalData, setModalData] = useState({});

    function handlerModal(data) {
        setModalData(data);
        setModalHandler(true);
    }

    return (
        <>
            <div className={classes.bottomNav}>
                <BottomNavigation
                    value={value}
                    onChange={(event, newValue) => {
                        setValue(newValue);
                    }}
                    className={classes.paperMain}
                >
                    {mainButtons.map((btn, i) => (
                        <BottomNavigationAction
                            key={`paper ${i}`}
                            icon={
                                <Slide
                                    direction="up"
                                    in={true}
                                    mountOnEnter
                                    timeout={400 * i + 400}
                                >
                                    <Fab
                                        className={classes.fabButton}
                                        aria-label={btn.name}
                                        size="medium"
                                    >
                                        {btn.icon}
                                    </Fab>
                                </Slide>
                            }
                            showLabel
                            onClick={() => handlerModal(btn)}
                            label={
                                <Paper className={classes.tipBtn}>
                                    <Typography className={classes.textBtn}>
                                        {btn.name}
                                    </Typography>
                                </Paper>
                            }
                            value={i}
                        />
                    ))}
                </BottomNavigation>
            </div>

            <ModalContent
                data={modalData}
                tabi={modalData.id}
                open={modalHandler}
                handleClose={() => {
                    setModalHandler(false);
                    setValue(null);
                }}
            />
        </>
    );
};

const mainButtons = [
    {
        id: 'prov',
        icon: <BeenhereIcon style={{ color: colores.darkBlueNav }} />,
        name: 'Gobierno Presente'
    },
    {
        id: 'ejes',
        icon: <DashboardIcon style={{ color: colores.darkBlueNav }} />,
        name: `Ejes de Trabajo`
    },
    {
        id: 'proyectos',
        icon: <BookmarksIcon style={{ color: colores.darkBlueNav }} />,
        name: 'Proyectos en el Concejo'
    },
    {
        id: 'ciudad',
        icon: <ApartmentIcon style={{ color: colores.darkBlueNav }} />,
        name: 'La Ciudad que Queremos'
    }
];

const useStyles = makeStyles((theme) => ({
    circularBtn: {
        borderWidth: 2,
        borderColor: 'blue'
    },
    paperMain: {
        marginRight: 'auto',
        marginLeft: 'auto',
        height: 55,
        backgroundColor: colores.gamBdark,
        width: 530,
        borderRadius: 10
    },
    bottomNav: {
        position: 'absolute',
        bottom: 40,
        alignSelf: 'center'
    },
    tipBtn: {
        marginTop: 5,
        padding: 4,
        backgroundColor: colores.mainLight2
    },
    textBtn: {
        fontSize: 12,
        letterSpacing: 1,
        color: colores.gamBdarkfull
    },
    fabButton: {
        transition: `${theme.transitions.create(['backgroundColor'])}`,
        '&:hover': {
            backgroundColor: colores.mainDark
        }
    }
}));

export default NavigationBar;
