import {
    Paper,
    Typography,
    Card,
    CardMedia,
    makeStyles,
    Grid,
    Button,
    Fab
} from '@material-ui/core';
import QuestionAnswerIcon from '@material-ui/icons/QuestionAnswer';
import React, { useState } from 'react';
import { isMobile } from 'react-device-detect';
import Swal from 'sweetalert2';
import JuegoAmbiente from '../components/juegoAmbiente';

import BtnGradiantBlue from './btnGradiantBlue';
import colores from '../styles/colors';
const EjesContent = ({ data }) => {
    const classes = useStyles(data);
    const [inOut, setInOut] = useState(2);
    const [juegoAmbiente, setJuegoAmbiente] = useState(false);

    return (
        <div className={classes.marginContainer}>
            <Grid container spacing={3}>
                <Grid item xs={12} sm={6}>
                    <Card elevation={5} className={classes.imgP}>
                        <CardMedia
                            image={data.img}
                            style={{
                                height: '100%'
                            }}
                        />
                    </Card>
                </Grid>
                <Grid item xs={12} sm={/* data.id === 'ambiente' ? 4 :  */ 6}>
                    <Paper elevation={5} className={classes.paperText}>
                        <Typography className={classes.letter}>
                            {data.text}
                        </Typography>
                    </Paper>
                </Grid>
                {/* {data.id === 'ambiente' && (
                    <Grid item sm={2}>
                        <div
                            onClick={() => setJuegoAmbiente(true)}
                            className={classes.root}
                            onMouseEnter={() => setInOut(5)}
                            onMouseLeave={() => setInOut(2)}
                        >
                            <BtnGradiantBlue
                                txt1={'Preguntas y Respuestas'}
                                txt2={'Iniciar Juego'}
                                txt3={`contesta preguntas y enterate cuanto sabes
                                    sobre el medio ambiente en la ciudad de
                                    Santa Fe`}
                            />
                        </div>
                    </Grid>
                )} */}
                <Grid item xs={12} sm={4}>
                    <Paper elevation={5} className={classes.paperCards}>
                        <Typography className={classes.letter}>
                            {data.card0.txtT}
                        </Typography>
                        <Typography className={classes.textCard}>
                            {data.card0.txtA}
                        </Typography>
                        <Typography className={classes.textCard}>
                            {data.card0.txtB}
                        </Typography>
                        <Typography className={classes.textCard}>
                            {data.card0.txtC}
                        </Typography>
                        <Typography className={classes.textCard}>
                            {data.card0.txtD}
                        </Typography>
                        <Typography className={classes.textCard}>
                            {data.card0.txtE}
                        </Typography>
                        <Typography className={classes.textCard}>
                            {data.card0.txtF}
                        </Typography>
                        {data.card0.link && (
                            <Button
                                variant="outlined"
                                onClick={() => window.open(data.card0.link)}
                            >
                                +Info
                            </Button>
                        )}
                    </Paper>
                </Grid>
                <Grid item xs={12} sm={4}>
                    <Card elevation={5} className={classes.cMedia}>
                        <CardMedia
                            image={data.card2.img}
                            style={{ height: '100%' }}
                        />
                    </Card>
                </Grid>
                <Grid item xs={12} sm={4}>
                    <Paper elevation={5} className={classes.paperCards}>
                        <Typography className={classes.letter}>
                            {data.card1.txtT}
                        </Typography>
                        <Typography className={classes.textCard}>
                            {data.card1.txtA}
                        </Typography>
                        <Typography className={classes.textCard}>
                            {data.card1.txtB}
                        </Typography>
                    </Paper>
                </Grid>
            </Grid>
            {data.id === 'ambiente' && (
                <JuegoAmbiente
                    open={juegoAmbiente}
                    setOpen={setJuegoAmbiente}
                />
            )}
        </div>
    );
};

const useStyles = makeStyles((theme) => ({
    marginContainer: {
        width: isMobile && '94%',
        margin: isMobile && 'auto'
    },
    paperText: {
        padding: isMobile ? 15 : 25,

        borderRadius: 15,
        height: !isMobile && 320,
        overflow: 'auto',
        background: (data) => {
            switch (data.id) {
                case 'ambiente':
                    return theme.backgroundGreen;
                    break;
                case 'produccion':
                    return theme.backgroundOrange;
                    break;
                case 'genero':
                    return theme.backgroundPurple;
                    break;
                case 'barrios':
                    return theme.backgroundBlue;
                    break;
                default:
                    return theme.backgroundJor;
                    break;
            }
        }
    },
    letter: {
        letterSpacing: 1.2,
        fontSize: isMobile ? 12 : 16,
        color: '#000'
    },
    imgP: {
        height: isMobile ? 120 : 320,
        borderRadius: 15
    },
    paperCards: {
        padding: '15px 25px',
        borderRadius: 15,
        height: !isMobile && 240,
        overflow: 'auto',
        background: (data) => {
            switch (data.id) {
                case 'ambiente':
                    return theme.backgroundGreen;
                    break;
                case 'produccion':
                    return theme.backgroundOrange;
                    break;
                case 'genero':
                    return theme.backgroundPurple;
                    break;
                case 'barrios':
                    return theme.backgroundBlue;
                    break;
                default:
                    return theme.backgroundJor;
                    break;
            }
        }
    },
    textCard: {
        textAlign: 'left',
        marginTop: 3,
        fontSize: isMobile ? 12 : 14,
        overflowY: 'scroll',
        boxSizing: 'border-box'
    },
    cMedia: {
        height: 240,
        borderRadius: 15
    },
    root: {
        height: '100%',
        borderRadius: 15,
        textAlign: 'center'
    },
    '@global': {
        '*::-webkit-scrollbar': {
            width: '0em'
        }
    }
}));

export default EjesContent;
