import React from 'react';
import './App.css';
import Home from './pages/index';
import { ThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import MessengerCustomerChat from 'react-messenger-customer-chat';
import ProtectedRoute from './helpers/protectedRoute';
import ScrollToTop from './helpers/scrollTop';
import PrensaDash from './pages/prensaDash';
import Login from './pages/login';
import FormuPart from './pages/formuPart';
import FormProvider from './context/formContext';

const theme = createMuiTheme({
    typography: {
        fontFamily: ['Sora', 'sans-serif'].join(',')
    }
});

const themeGradiant = {
    backgroundBlue:
        'linear-gradient(to right top, #a59dda, #958ad7, #8577d3, #7564ce, #6550c9)',
    backgroundGreen:
        'linear-gradient(to right top, #abffb1, #98f49a, #84ea82, #71df69, #5ed44f)',
    backgroundBlueDark:
        'linear-gradient(to left top, #0472c2, #0d65b2, #1158a1, #134b91, #123f81)',
    backgroundOrange:
        'linear-gradient(to right top, #daca9d, #d4bb82, #d0ab69, #cc9a50, #c88938)',
    backgroundPurple:
        'linear-gradient(to right top, #cf9dda, #c98bd6, #c278d2, #bb65ce, #b450c9)',
    backgroundJor:
        'linear-gradient(to right top, #76ddff, #62d2f9, #4cc6f3, #32bbee, #00afe8)',
    backgroundCool1:
        'linear-gradient(to right top, #d16ba5, #c777b9, #ba83ca, #aa8fd8, #9a9ae1, #8aa7ec, #79b3f4, #69bff8, #52cffe, #41dfff, #46eefa, #5ffbf1)'
};

function App() {
    return (
        <ThemeProvider theme={theme}>
            <ThemeProvider theme={themeGradiant}>
                <MessengerCustomerChat
                    language="es_AR"
                    pageId={process.env.REACT_APP_PAGE_FB_ID}
                    appId={process.env.REACT_APP_FB_ID}
                    htmlRef="https://jorweb.herokuapp.com/"
                />
                <CssBaseline />

                <FormProvider>
                    <Router>
                        <ScrollToTop />
                        <Switch>
                            <Route exact path="/" component={Home} />

                            <Route exact path="/logg" component={Login} />
                            <Route exact path="/sumate" component={FormuPart} />

                            <ProtectedRoute
                                exact
                                path="/fform"
                                component={PrensaDash}
                            />
                            <Route path="*" component={Home} />
                        </Switch>
                    </Router>
                </FormProvider>
            </ThemeProvider>
        </ThemeProvider>
    );
}

export default App;
