const Transition = React.forwardRef(function Transition(props, ref) {
    return <Zoom ref={ref} {...props} />;
});

export default function JuegoAmbiente({ open, setOpen }) {
    const classes = useStyles();
    return (
        <Dialog
            fullScreen
            open={open}
            TransitionComponent={Transition}
            keepMounted
            onClose={() => setOpen(false)}
            classes={classes.dialogIndex}
        >
            <DialogContent
                style={{
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    margin: 'auto',
                    backgroundColor: colores.ejeCity,
                    width: '100%'
                }}
            >
                <Card
                    elevation={5}
                    style={{
                        padding: 30,
                        maxWidth: '50%',
                        margin: 'auto',
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'flex-end'
                    }}
                >
                    <Fab size="small" onClick={() => setOpen(false)}>
                        <CloseIcon />
                    </Fab>
                    <CardContent>
                        <JuegoAmbienteContent setOpen={setOpen} />
                    </CardContent>
                </Card>
            </DialogContent>
        </Dialog>
    );
}

function JuegoAmbienteContent({}) {
    const classes = useStyles();
    const [activeStep, setActiveStep] = useState(0);
    const [checkedd, setChekedd] = useState(null);
    const [puntaje, setPuntaje] = useState(0);
    const steps = getSteps();

    const handleNext = () => {
        if (activeStep !== 0) {
            let val = 0;
            if (checkedd === steps[activeStep].correct) val = 2;
            else val = -1;
            setPuntaje((prevPuntaje) => prevPuntaje + val);
            setActiveStep((prevActiveStep) => prevActiveStep + 1);
            setChekedd(null);
        } else setActiveStep((prevActiveStep) => prevActiveStep + 1);
    };

    const handleReset = () => {
        setActiveStep(0);
        setPuntaje(0);
    };

    const handlerRespPuntos = () => {
        let text;
        if (puntaje === 20)
            text = 'acertaste todas las respuestas, sos experto';
        if (puntaje > 15 && puntaje < 20)
            text = 'puntaje max 15, sabes mucho sobre medio ambiente';
        if (puntaje > 8 && puntaje < 15)
            text = 'tienes conocimientos, igual puedes seguir mejorando';
        if (puntaje > 0 && puntaje < 8)
            text = 'tienes una vaga idea, sigue aprendiendo';
        if (puntaje < 0) text = 'no te fue nada bien, deberias aprender mas';

        return text;
    };

    if (activeStep === 0) {
        return (
            <div
                style={{
                    textAlign: 'center',
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center'
                }}
            >
                <Typography
                    style={{
                        fontSize: isMobile ? 22 : 28,
                        color: colores.darkBlueNav
                    }}
                >
                    Responde las preguntas y descubre cuanto sabes sobre el
                    medio ambiente en la ciudad de Santa Fe
                </Typography>
                <Divider variant="middle" style={{ margin: 10 }} />
                <Typography
                    style={{ fontSize: 20, color: colores.darkBlueNav }}
                >
                    Cudado, si respondes mal se restaran puntos !
                </Typography>
                <Divider variant="middle" style={{ margin: 10 }} />

                <Button
                    variant="contained"
                    color="primary"
                    onClick={handleNext}
                    style={{ fontSize: 22, backgroundColor: colores.letrajor }}
                    startIcon={
                        <CheckCircleOutlineIcon
                            style={{
                                fontSize: 30
                            }}
                        />
                    }
                >
                    Iniciar Juego
                </Button>
            </div>
        );
    }
    if (activeStep === steps.length) {
        return (
            <div
                style={{
                    textAlign: 'center'
                }}
            >
                <Typography
                    style={{ fontSize: 28, color: colores.darkBlueNav }}
                >
                    Terminaste de contestar todas las preguntas
                </Typography>

                <Typography
                    style={{
                        fontSize: 18,
                        color: colores.darkBlueNav,
                        marginTop: 10
                    }}
                >
                    {puntaje} puntos de un total de 20
                </Typography>
                <Divider variant="middle" style={{ margin: 30 }} />
                <Typography
                    style={{ fontSize: 22, color: colores.darkBlueNav }}
                >
                    {handlerRespPuntos()}
                </Typography>
                <Divider variant="middle" style={{ margin: 30 }} />
                <Button
                    style={{ fontSize: 22 }}
                    variant="contained"
                    color="primary"
                    onClick={handleReset}
                >
                    Reiniciar
                </Button>
            </div>
        );
    }

    return (
        <div className={classes.root}>
            <MobileStepper
                variant="progress"
                steps={11}
                position="static"
                activeStep={activeStep}
                LinearProgressProps={{
                    style: { width: '100%' }
                }}
                className={classes.progressbar}
            />

            <div>
                <Typography className={classes.instructions}>
                    {getStepContent(activeStep).preg.toUpperCase()}
                </Typography>
                <Divider variant="middle" style={{ margin: 30 }} />
                <div>
                    {getStepContent(activeStep).respuestas.map((resp, i) => (
                        <div
                            key={i}
                            style={{
                                display: 'flex',
                                flexDirection: 'row'
                            }}
                        >
                            <FormControlLabel
                                style={{ fontSize: 30 }}
                                control={
                                    <Checkbox
                                        checked={checkedd === i}
                                        onChange={() => setChekedd(i)}
                                        color="primary"
                                    />
                                }
                                label={
                                    <Typography
                                        style={{
                                            fontSize: 20,
                                            color: colores.darkBlueNav
                                        }}
                                    >
                                        {resp.txt}
                                    </Typography>
                                }
                            />
                        </div>
                    ))}
                </div>
                <Divider variant="middle" style={{ margin: 30 }} />
                <Button
                    variant="contained"
                    color="primary"
                    onClick={handleNext}
                    disabled={checkedd === null}
                >
                    {activeStep === steps.length - 1
                        ? 'Terminar !'
                        : 'Siguiente'}
                </Button>
            </div>
        </div>
    );
}
