import {
    AppBar,
    Dialog,
    IconButton,
    makeStyles,
    Slide,
    Toolbar,
    Typography
} from '@material-ui/core';
import { forwardRef, useState } from 'react';
import { isMobile } from 'react-device-detect';
import colores from '../styles/colors';
import CloseIcon from '@material-ui/icons/Close';
import TabsProv from '../components/tabsProv';
import ProvinciaContent from '../components/provContent/provinciaContent';
import ProyectContent from '../components/proyectContent/proyectContent';
import CityContent from '../components/cityContent/cityContent';
import ContentEjes from '../components/ejesContent/contentEjes';

const ModalContent = ({ data, tabi, open, handleClose }) => {
    const classes = useStyles();
    const [value, setValue] = useState(0);

    function handlerContent() {
        switch (tabi) {
            case 'prov':
                return <ProvinciaContent val={value} />;

            case 'ejes':
                return <ContentEjes />;

            case 'proyectos':
                return <ProyectContent />;
            case 'ciudad':
                return <CityContent />;
            default:
                break;
        }
    }

    return (
        <div>
            <Dialog
                fullScreen
                open={open}
                onClose={handleClose}
                TransitionComponent={Transition}
            >
                <AppBar className={classes.appBar}>
                    <Toolbar
                        style={{
                            backgroundColor: colores.mainLightfull,
                            flexDirection: 'row'
                        }}
                    >
                        <IconButton
                            edge="start"
                            color={colores.mainDarkfull}
                            onClick={handleClose}
                            aria-label="close"
                        >
                            <CloseIcon />
                        </IconButton>
                        <Typography variant="h6" className={classes.title}>
                            {data.name}
                        </Typography>
                    </Toolbar>
                    {isMobile && tabi === 'prov' && (
                        <TabsProv value={value} setValue={setValue} />
                    )}
                </AppBar>
                <div className={classes.content}>{handlerContent()}</div>
            </Dialog>
        </div>
    );
};

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const useStyles = makeStyles((theme) => ({
    appBar: {
        position: 'sticky'
    },
    content: {
        height: '1040%',
        paddingLeft: isMobile ? 10 : '7.5%',
        paddingRight: isMobile ? 10 : '7.5%',
        paddingTop: 40,
        paddingBottom: 20,
        backgroundColor: colores.fondoLight
    },
    title: {
        color: colores.mainDarkfull
    },
    tabs: {
        borderRight: `1px solid ${theme.palette.divider}`
    }
}));

export default ModalContent;
