import React, { useState } from 'react';

import {
    Paper,
    Grid,
    Typography,
    CardMedia,
    Card,
    CircularProgress
} from '@material-ui/core';
import { billeteraData } from '../../info/provincia';
import { isIOS, isMobile } from 'react-device-detect';
import provStyles from '../../styles/provStyle';
import StorefrontIcon from '@material-ui/icons/Storefront';
import AndroidIcon from '@material-ui/icons/Android';
import AppleIcon from '@material-ui/icons/Apple';
import ContactSupportIcon from '@material-ui/icons/ContactSupport';
import bill from '../../assets/ejesProv/jorBilletera.jpg';
import useProgressiveImage from '../../helpers/progressiveImg';
import BtnGradiantJor from '../../custom/divGradiantJor';

const BilleteraContent = () => {
    const [select, setSelect] = useState('');
    const classes = provStyles();
    const imag = useProgressiveImage(bill);

    if (!imag) {
        return (
            <CircularProgress
                style={{
                    color: '#00AFE8',
                    marginTop: isMobile ? 20 : 80,
                    marginLeft: !isMobile && '40%'
                }}
                size={isMobile ? 40 : 70}
            />
        );
    }
    return (
        <div className={classes.marginContainer}>
            <Grid container spacing={isMobile ? 2 : 3}>
                <Grid item xs={12} sm={12}>
                    <Card
                        elevation={5}
                        style={{
                            height: isMobile ? 120 : 240,
                            borderRadius: 15
                        }}
                    >
                        <CardMedia
                            image={imag}
                            style={{
                                height: '100%',
                                padding: isMobile ? 0 : 180
                            }}
                        />
                    </Card>
                </Grid>
                {isMobile && (
                    <Typography style={{ fontSize: 22, margin: 'auto' }}>
                        Billetera Santa Fe
                    </Typography>
                )}
                <Grid item xs={12} sm={8}>
                    <Grid container spacing={isMobile ? 2 : 3}>
                        <Grid item sm={12}>
                            <Paper elevation={3} className={classes.cards}>
                                <Typography className={classes.textTitle}>
                                    {billeteraData.text}
                                </Typography>
                            </Paper>
                        </Grid>
                        <Grid item xs={12} sm={4}>
                            <Paper
                                elevation={3}
                                className={classes.cardCenter}
                                style={{ height: isMobile && 70 }}
                            >
                                <Typography className={classes.textBig}>
                                    {billeteraData.card4.ico}
                                    {billeteraData.card4.txtA}
                                </Typography>
                                <Typography className={classes.textmed}>
                                    {billeteraData.card4.txtB}
                                </Typography>
                            </Paper>
                        </Grid>
                        <Grid item xs={6} sm={4}>
                            <Paper elevation={3} className={classes.cardCenter}>
                                <Typography className={classes.textBig}>
                                    <img
                                        src={billeteraData.card2.ico}
                                        alt={'Billetera Santa Fe'}
                                        style={{
                                            height: 28,
                                            width: 28,
                                            marginRight: 10,
                                            color: 'red'
                                        }}
                                    />

                                    {billeteraData.card2.txtA}
                                </Typography>
                                <Typography className={classes.textmed}>
                                    {billeteraData.card2.txtB}
                                </Typography>
                            </Paper>
                        </Grid>
                        <Grid item xs={6} sm={4}>
                            <Paper elevation={3} className={classes.cardCenter}>
                                <Typography className={classes.textBig}>
                                    <img
                                        alt="Billetera Santa Fe 2"
                                        src={billeteraData.card3.txtC}
                                        style={{
                                            height: 28,
                                            width: 28,
                                            marginRight: 10
                                        }}
                                    />
                                    {billeteraData.card3.txtA}
                                </Typography>
                                <Typography className={classes.textmed}>
                                    {billeteraData.card3.txtB}
                                </Typography>
                            </Paper>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={12} sm={4}>
                    <Grid container spacing={3}>
                        <Grid item xs={12} sm={12}>
                            <Paper
                                className={classes.cardVert}
                                elevation={select !== 'registro' ? 3 : 5}
                                onMouseEnter={() => setSelect('registro')}
                                onMouseLeave={() => setSelect('')}
                                onClick={() =>
                                    window.open(billeteraData.card0.url)
                                }
                            >
                                <Typography className={classes.textGrl}>
                                    {!isMobile && <StorefrontIcon />}
                                    {billeteraData.card0.txtA}
                                </Typography>
                                {isMobile && <StorefrontIcon />}
                                <Typography className={classes.textmed}>
                                    {billeteraData.card0.txtB}
                                </Typography>
                            </Paper>
                        </Grid>
                    </Grid>

                    <Paper
                        className={classes.cardVert}
                        elevation={select !== 'app' ? 3 : 5}
                        onMouseEnter={() => setSelect('app')}
                        onMouseLeave={() => setSelect('')}
                        onClick={() =>
                            window.open(
                                isIOS
                                    ? billeteraData.card1.urlIos
                                    : billeteraData.card1.url
                            )
                        }
                    >
                        <Typography className={classes.textGrl}>
                            {isMobile !== true && <AndroidIcon />}
                            {billeteraData.card1.txtA}
                        </Typography>
                        {isMobile && (isIOS ? <AppleIcon /> : <AndroidIcon />)}
                        <Typography className={classes.textmed}>
                            {billeteraData.card1.txtB}
                        </Typography>
                    </Paper>

                    <Paper
                        className={classes.cardVert}
                        elevation={select !== 'mas' ? 3 : 5}
                        onMouseEnter={() => setSelect('mas')}
                        onMouseLeave={() => setSelect('')}
                        onClick={() => window.open(billeteraData.card5.url)}
                    >
                        <Typography className={classes.textGrl}>
                            {isMobile !== true && <ContactSupportIcon />}
                            {billeteraData.card5.txtA}
                        </Typography>
                        {isMobile && <ContactSupportIcon />}
                        <Typography className={classes.textmed}>
                            {billeteraData.card5.txtB}
                        </Typography>
                    </Paper>
                </Grid>
            </Grid>
        </div>
    );
};

export default BilleteraContent;
